import {
	myRequestGet
} from '@/utils/zgrequest.js'
import parse from '@/utils/htmlparser.js';
import {
	NewsDetailUrl
} from '@/utils/api.js'
export default {
	data() {
		return {
			id: "",
			newsDetail: {},
			htmlNodes: []
		}
	},
	onLoad(options) {
		this.id = options.id;
		this.getNewsDetail()
	},
	methods: {
		async getNewsDetail() {
			const res = await myRequestGet(NewsDetailUrl + this.id)
			this.newsDetail = res.message[0]




		}
	}
}
